.card {
  background: rgb(245, 0, 0);
  margin-left: 50px;
  border: 20px;
  border-color: black;
  border-radius: 10px;
  height: 200px;
  margin: 1rem;
  position: relative;
  width: 200px;
  -webkit-box-shadow: 0 6px 12px rgb(98, 98, 98), 0 3px 6px rgb(85, 85, 85);
          box-shadow: 0 6px 12px rgb(98, 98, 98), 0 3px 6px rgb(85, 85, 85);
  text-align: left;
}

.card > .img-container {
  border-radius: 10px;
  height: 100%;
  overflow: hidden;
  text-align: center;
  background: -webkit-radial-gradient(rgb(230, 14, 14), rgb(230, 169, 40));
  background: -o-radial-gradient(rgb(230, 14, 14), rgb(230, 169, 40));
  background: radial-gradient(rgb(230, 14, 14), rgb(230, 169, 40));
}

.card > .img-container > img {
  width: 80%
}

.card > .img-container:hover {
  
    transform:rotate(360deg);
    -ms-transform:rotate(360deg);
    -webkit-transform:rotate(360deg);
}



